.skills {
  background-color: var(--section-bg);
  position: relative;
}

.skills::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--accent-teal), var(--accent-gold), var(--accent-blue));
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
}

.skill-category {
  background-color: var(--card-bg);
  border: 2px solid var(--accent-blue);
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: var(--card-shadow);
}

.skill-category:hover {
  border-color: var(--accent-gold);
  transform: translateY(-5px);
  box-shadow: var(--card-shadow-hover);
}

.category-header {
  background-color: var(--accent-blue);
  padding: 20px 25px;
  display: flex;
  align-items: center;
  gap: 15px;
  border-bottom: 2px solid var(--accent-gold);
}

.category-icon {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #ffffff;
}

.category-icon svg {
  font-size: 1.8rem;
  color: #ffffff;
}

.category-title {
  color: #ffffff;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.skills-list {
  padding: 25px;
}

.skill-item {
  margin-bottom: 20px;
}

.skill-item:last-child {
  margin-bottom: 0;
}

.skill-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.skill-name {
  color: var(--text-dark);
  font-weight: 600;
  font-size: 1.1rem;
}

.skill-level {
  color: var(--accent-gold);
  font-weight: 700;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.skill-bar {
  width: 100%;
  height: 6px;
  background-color: var(--border-color);
  position: relative;
  overflow: hidden;
}

.skill-progress {
  height: 100%;
  transition: width 1s ease;
  position: relative;
}

.skill-progress::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.skills-summary {
  display: flex;
  justify-content: center;
}

.summary-card {
  background-color: var(--card-bg);
  border: 2px solid var(--accent-teal);
  padding: 30px;
  max-width: 600px;
  text-align: center;
  box-shadow: var(--card-shadow);
}

.summary-card h3 {
  color: var(--text-dark);
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 25px;
  text-transform: uppercase;
  letter-spacing: 1px;
  border-bottom: 2px solid var(--accent-teal);
  padding-bottom: 12px;
}

.focus-areas {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 15px;
}

.focus-area {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 15px;
  background-color: var(--hover-bg);
  border: 2px solid var(--accent-blue);
  transition: all 0.3s ease;
}

.focus-area:hover {
  border-color: var(--accent-gold);
  transform: translateY(-2px);
  box-shadow: var(--card-shadow);
}

.focus-area svg {
  font-size: 1.6rem;
  color: var(--accent-gold);
  min-width: 20px;
}

.focus-area span {
  color: var(--text-dark);
  font-weight: 600;
  font-size: 1.1rem;
  text-align: left;
}

@media (max-width: 1024px) {
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 20px;
  }
}

@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .category-header {
    padding: 15px 20px;
  }

  .category-icon {
    width: 45px;
    height: 45px;
  }

  .category-icon svg {
    font-size: 1.6rem;
  }

  .category-title {
    font-size: 1.3rem;
  }

  .skills-list {
    padding: 20px;
  }

  .skill-item {
    margin-bottom: 15px;
  }

  .skill-name {
    font-size: 1.05rem;
  }

  .skill-level {
    font-size: 0.95rem;
  }

  .summary-card {
    padding: 25px 20px;
  }

  .summary-card h3 {
    font-size: 1.5rem;
  }

  .focus-areas {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .focus-area {
    padding: 12px;
  }

  .focus-area span {
    font-size: 1.05rem;
  }
}

@media (max-width: 480px) {
  .category-header {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }

  .skills-list {
    padding: 15px;
  }

  .skill-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }

  .summary-card {
    padding: 20px 15px;
  }

  .summary-card h3 {
    font-size: 1.4rem;
  }

  .focus-area {
    flex-direction: column;
    text-align: center;
    gap: 8px;
  }

  .focus-area span {
    text-align: center;
  }
}
