.footer {
  background-color: var(--primary-dark);
  border-top: 2px solid var(--accent-blue);
  padding: 40px 0;
  position: relative;
}

.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--accent-blue), var(--accent-gold), var(--accent-teal));
}

.footer-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-text {
  text-align: center;
}

.footer-text p {
  color: var(--text-medium);
  font-size: 1rem;
  margin: 0;
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer {
    padding: 30px 0;
  }

  .footer-text p {
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 25px 0;
  }

  .footer-text p {
    font-size: 0.9rem;
  }
}
