.blog {
  background-color: var(--section-bg);
  position: relative;
  padding: 60px 0;
}

.blog::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--accent-blue), var(--accent-gold), var(--accent-teal));
}

.blog-loading,
.blog-error,
.blog-empty {
  text-align: center;
  padding: 60px 20px;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid var(--accent-blue);
  border-top: 3px solid var(--accent-gold);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.blog-loading p,
.blog-error p,
.blog-empty p {
  color: var(--text-medium);
  font-size: 1.1rem;
  margin-bottom: 30px;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.blog-card {
  background-color: var(--card-bg);
  border: 2px solid var(--accent-blue);
  padding: 30px;
  transition: all 0.3s ease;
  box-shadow: var(--card-shadow);
  position: relative;
  overflow: hidden;
}

.blog-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(180deg, var(--accent-blue), var(--accent-gold));
  transform: scaleY(0);
  transition: transform 0.3s ease;
}

.blog-card:hover::before {
  transform: scaleY(1);
}

.blog-card:hover {
  border-color: var(--accent-gold);
  transform: translateY(-10px);
  box-shadow: var(--card-shadow-hover);
}

.blog-card-header {
  margin-bottom: 20px;
}

.blog-category {
  display: inline-block;
  background-color: var(--accent-blue);
  color: #ffffff;
  padding: 6px 12px;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 15px;
}

.blog-meta {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.blog-date,
.blog-reading-time {
  display: flex;
  align-items: center;
  gap: 6px;
  color: var(--text-medium);
  font-size: 0.9rem;
  font-weight: 500;
}

.blog-date svg,
.blog-reading-time svg {
  font-size: 0.8rem;
  color: var(--accent-teal);
}

.blog-card-content {
  margin-bottom: 25px;
}

.blog-title {
  color: var(--text-dark);
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 15px;
  line-height: 1.4;
  transition: color 0.3s ease;
}

.blog-card:hover .blog-title {
  color: var(--accent-blue);
}

.blog-excerpt {
  color: var(--text-medium);
  font-size: 1rem;
  line-height: 1.6;
  margin: 0;
}

.blog-card-footer {
  border-top: 1px solid var(--border-color);
  padding-top: 20px;
}

.blog-link {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: var(--accent-blue);
  font-weight: 600;
  font-size: 1rem;
  text-decoration: none;
  transition: all 0.3s ease;
  padding: 8px 0;
  border-bottom: 2px solid transparent;
}

.blog-link:hover {
  color: var(--accent-gold);
  border-bottom-color: var(--accent-gold);
  transform: translateX(5px);
}

.blog-link svg {
  font-size: 0.9rem;
}

.blog-cta {
  text-align: center;
  margin-top: 60px;
  padding: 40px 20px;
  background-color: var(--card-bg);
  border: 2px solid var(--accent-blue);
  border-radius: 8px;
  box-shadow: var(--card-shadow);
}

.blog-cta p {
  color: var(--text-medium);
  font-size: 1.2rem;
  margin-bottom: 25px;
}

.blog-cta .btn {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 15px 30px;
  font-size: 1.1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.3s ease;
}

.blog-cta .btn:hover {
  transform: translateY(-3px);
  box-shadow: var(--card-shadow-hover);
}

/* Responsive Design */
@media (max-width: 1200px) {
  .blog-grid {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 25px;
  }
}

@media (max-width: 768px) {
  .blog {
    padding: 50px 0;
  }
  
  .blog-grid {
    grid-template-columns: 1fr;
    max-width: 500px;
    margin: 30px auto 0;
    gap: 20px;
  }
  
  .blog-card {
    padding: 25px 20px;
  }
  
  .blog-title {
    font-size: 1.3rem;
  }
  
  .blog-meta {
    gap: 15px;
  }
  
  .blog-cta {
    margin-top: 50px;
    padding: 30px 20px;
  }
  
  .blog-cta p {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .blog {
    padding: 40px 0;
  }
  
  .blog-card {
    padding: 20px 15px;
  }
  
  .blog-title {
    font-size: 1.2rem;
  }
  
  .blog-excerpt {
    font-size: 0.95rem;
  }
  
  .blog-meta {
    flex-direction: column;
    gap: 8px;
  }
  
  .blog-cta {
    margin-top: 40px;
    padding: 25px 15px;
  }
  
  .blog-cta p {
    font-size: 1rem;
  }
  
  .blog-cta .btn {
    padding: 12px 25px;
    font-size: 1rem;
  }
}
