.projects {
  background-color: var(--section-bg);
  position: relative;
}

.projects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--accent-gold), var(--accent-blue), var(--accent-teal));
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
}

.project-card {
  background-color: var(--card-bg);
  border: 2px solid var(--accent-blue);
  overflow: hidden;
  transition: all 0.3s ease;
  position: relative;
  box-shadow: var(--card-shadow);
}

.project-card:hover {
  border-color: var(--accent-gold);
  box-shadow: var(--card-shadow-hover);
  transform: translateY(-5px);
}

.project-card.featured {
  border-color: var(--accent-gold);
  border-width: 3px;
}

.project-card.featured::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--accent-gold), var(--accent-blue));
}

.project-header {
  background-color: var(--accent-blue);
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid var(--accent-gold);
}

.project-category {
  color: #ffffff;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.featured-badge {
  background-color: var(--accent-gold);
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 4px 10px;
  border: 2px solid #ffffff;
}

.project-content {
  padding: 25px 20px;
}

.project-title {
  color: var(--text-dark);
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 12px;
  line-height: 1.3;
}

.project-description {
  color: var(--text-medium);
  line-height: 1.6;
  margin-bottom: 20px;
  font-size: 1.1rem;
}

.project-technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 15px;
}

.tech-tag {
  background-color: var(--accent-teal);
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 600;
  padding: 4px 10px;
  border: 1px solid var(--accent-gold);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
}

.tech-tag:hover {
  background-color: var(--accent-gold);
  transform: translateY(-2px);
}

.project-footer {
  background-color: var(--hover-bg);
  padding: 15px 20px;
  border-top: 2px solid var(--border-color);
}

.project-links {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.project-link {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #ffffff;
  font-weight: 600;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 8px 12px;
  background-color: var(--accent-blue);
  border: 2px solid var(--accent-blue);
  transition: all 0.3s ease;
  text-decoration: none;
}

.project-link:hover {
  background-color: transparent;
  color: var(--accent-blue);
  border-color: var(--accent-blue);
}

.project-link svg {
  font-size: 1.05rem;
}

.projects-cta {
  text-align: center;
  padding: 30px 0;
  border-top: 2px solid var(--border-color);
}

.projects-cta p {
  color: var(--text-medium);
  font-size: 1.15rem;
  margin-bottom: 20px;
}

.projects-cta .btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-size: 1.05rem;
}

.projects-cta .btn svg {
  font-size: 1.25rem;
}

@media (max-width: 1024px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
  }
}

@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .project-header {
    padding: 12px 15px;
  }

  .project-content {
    padding: 20px 15px;
  }

  .project-footer {
    padding: 12px 15px;
  }

  .project-title {
    font-size: 1.4rem;
  }

  .project-description {
    font-size: 1.05rem;
  }

  .project-links {
    gap: 8px;
  }

  .project-link {
    padding: 6px 10px;
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .project-header {
    flex-direction: column;
    gap: 8px;
    align-items: flex-start;
  }

  .project-content {
    padding: 15px 12px;
  }

  .project-footer {
    padding: 12px 12px;
  }

  .project-title {
    font-size: 1.3rem;
  }

  .project-description {
    font-size: 1rem;
  }

  .tech-tag {
    font-size: 0.85rem;
    padding: 3px 8px;
  }

  .project-links {
    flex-direction: column;
  }

  .project-link {
    justify-content: center;
  }
}
