.theme-switcher {
  position: fixed;
  top: 100px;
  right: 30px;
  width: 50px;
  height: 50px;
  background: var(--accent-blue);
  border: 2px solid var(--accent-gold);
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 1001;
  transition: all 0.3s ease;
  box-shadow: var(--card-shadow);
}

.theme-switcher:hover {
  background: var(--accent-gold);
  border-color: var(--accent-blue);
  transform: translateY(-2px);
  box-shadow: var(--card-shadow-hover);
}

.theme-icon {
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

@media (max-width: 768px) {
  .theme-switcher {
    top: 90px;
    right: 20px;
    width: 45px;
    height: 45px;
  }
  
  .theme-icon {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .theme-switcher {
    top: 80px;
    right: 15px;
    width: 40px;
    height: 40px;
  }
  
  .theme-icon {
    font-size: 1rem;
  }
}
