.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  background: linear-gradient(135deg, var(--primary-dark) 0%, var(--section-bg) 100%);
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    linear-gradient(45deg, transparent 48%, var(--accent-blue) 50%, transparent 52%),
    linear-gradient(-45deg, transparent 48%, var(--accent-teal) 50%, transparent 52%);
  background-size: 60px 60px;
  opacity: 0.03;
  animation: gridMove 20s linear infinite;
}

@keyframes gridMove {
  0% { transform: translate(0, 0); }
  100% { transform: translate(60px, 60px); }
}

.hero-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  position: relative;
  z-index: 2;
  text-align: center;
}

.hero-content {
  margin-bottom: 60px;
}

.hero-text {
  max-width: 800px;
  margin: 0 auto;
}

.hero-title {
  font-size: 4rem;
  font-weight: 700;
  color: var(--text-dark);
  margin-bottom: 20px;
  line-height: 1.1;
  background: linear-gradient(135deg, var(--text-dark) 0%, var(--accent-gold) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  transition: all 0.3s ease;
}

.hero-subtitle {
  font-size: 1.5rem;
  color: var(--accent-blue);
  margin-bottom: 30px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: all 0.3s ease;
}

.hero-description {
  font-size: 1.2rem;
  color: var(--text-medium);
  margin-bottom: 30px;
  line-height: 1.8;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  transition: all 0.3s ease;
}

.hero-location {
  margin-bottom: 40px;
}

.location-text {
  color: var(--accent-teal);
  font-weight: 500;
  font-size: 1.1rem;
  transition: all 0.3s ease;
}

.hero-actions {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.hero-social {
  display: flex;
  justify-content: center;
}

.social-links {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  justify-content: center;
}

.social-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  color: var(--text-dark);
  padding: 20px;
  background-color: var(--card-bg);
  border: 2px solid var(--accent-blue);
  transition: all 0.3s ease;
  min-width: 100px;
  box-shadow: var(--card-shadow);
}

.social-link:hover {
  background-color: var(--accent-blue);
  border-color: var(--accent-gold);
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: var(--card-shadow-hover);
}

.social-link svg {
  font-size: 1.5rem;
}

/* Invert phone icon horizontally in Hero section */
.social-link:nth-child(3) svg {
  transform: scaleX(-1);
}

.social-label {
  font-size: 0.9rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

@media (max-width: 768px) {
  .hero {
    padding-top: 100px;
  }

  .hero-title {
    font-size: 3rem;
  }

  .hero-subtitle {
    font-size: 1.3rem;
  }

  .hero-description {
    font-size: 1.1rem;
  }

  .social-links {
    gap: 20px;
  }

  .social-link {
    min-width: 80px;
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2.5rem;
  }

  .hero-actions {
    flex-direction: column;
    align-items: center;
  }
}
