.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background-color: var(--header-bg);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
}

.header.scrolled {
  background-color: var(--header-bg);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.15);
}

.header-container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
}

.header-status {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 12px;
  background: rgba(107, 90, 51, 0.1);
  border: 1px solid rgba(107, 90, 51, 0.2);
  border-radius: 15px;
  transition: all 0.3s ease;
}

.header-status:hover {
  background: rgba(107, 90, 51, 0.15);
  border-color: rgba(107, 90, 51, 0.3);
}

.status-indicator {
  color: #22c55e;
  font-size: 0.8rem;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
}

.status-text {
  color: var(--text-medium);
  font-size: 0.8rem;
  font-weight: 500;
  letter-spacing: 0.5px;
}

.nav {
  display: flex;
  align-items: center;
}

.nav-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 40px;
}

.nav-item {
  margin: 0;
}

.nav-link {
  color: var(--text-dark);
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  padding: 8px 12px;
  border-radius: 6px;
  position: relative;
}

.nav-link:hover {
  background-color: rgba(107, 90, 51, 0.08);
  color: var(--accent-gold);
}

.nav-link::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--accent-gold);
  transition: width 0.3s ease;
}

.nav-link:hover::before {
  width: 100%;
}

.mobile-menu-btn {
  display: none;
  background: none;
  border: none;
  color: var(--text-dark);
  font-size: 1.5rem;
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: all 0.3s ease;
}

.mobile-menu-btn:hover {
  background-color: rgba(107, 90, 51, 0.08);
  color: var(--accent-gold);
}

@media (max-width: 768px) {
  .header-container {
    padding: 0 20px;
    height: 70px;
  }

  .header-status {
    padding: 4px 8px;
  }

  .status-text {
    font-size: 0.75rem;
  }

  .nav {
    position: fixed;
    top: 70px;
    left: 0;
    right: 0;
    background-color: var(--header-bg);
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.15);
    transform: translateY(-100%);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
  }

  .nav.mobile-open {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }

  .nav-list {
    flex-direction: column;
    gap: 0;
    padding: 20px;
  }

  .nav-item {
    width: 100%;
  }

  .nav-link {
    display: block;
    padding: 15px 20px;
    border-radius: 8px;
    font-size: 1.2rem;
  }

  .nav-link:hover {
    background-color: rgba(107, 90, 51, 0.1);
  }

  .mobile-menu-btn {
    display: block;
  }
}

@media (max-width: 480px) {
  .header-container {
    padding: 0 15px;
    height: 60px;
  }

  .header-status {
    padding: 3px 6px;
  }

  .status-text {
    font-size: 0.7rem;
  }

  .nav {
    top: 60px;
  }

  .nav-link {
    padding: 12px 15px;
    font-size: 1.1rem;
  }
}
