/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
  font-size: 18px;
}

/* CSS Variables for Light Theme (Default) */
:root {
  /* Light Theme Colors - Improved for Readability */
  --primary-dark: #ffffff;
  --primary-light: #f8fafc;
  --text-white: #0f172a;
  --text-gray: #475569;
  --border-color: #e2e8f0;
  
  /* Accent Colors (same for both themes) */
  --accent-blue: #315060;
  --accent-gold: #6B5A33;
  --accent-teal: #45606D;
  
  /* Additional Light Theme Colors - Enhanced for Readability */
  --card-bg: #ffffff;
  --card-border: #e2e8f0;
  --shadow-light: rgba(0, 0, 0, 0.05);
  --shadow-medium: rgba(0, 0, 0, 0.08);
  --shadow-heavy: rgba(0, 0, 0, 0.12);
  --hover-bg: #f1f5f9;
  --gradient-start: #ffffff;
  --gradient-end: #f8fafc;
  --section-bg: #f8fafc;
  --header-bg: rgba(255, 255, 255, 0.95);
  --card-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --card-shadow-hover: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --text-dark: #0f172a;
  --text-medium: #334155;
  --text-light: #64748b;
}

/* Dark Theme Colors */
[data-theme="dark"] {
  --primary-dark: #020404;
  --primary-light: #142127;
  --text-white: #ffffff;
  --text-gray: #b0b0b0;
  --border-color: #2a2a2a;
  
  /* Dark Theme Specific Colors */
  --card-bg: #142127;
  --card-border: #2a2a2a;
  --shadow-light: rgba(0, 0, 0, 0.3);
  --shadow-medium: rgba(0, 0, 0, 0.4);
  --shadow-heavy: rgba(0, 0, 0, 0.5);
  --hover-bg: #1a2a35;
  --gradient-start: #020404;
  --gradient-end: #142127;
  --section-bg: #142127;
  --header-bg: rgba(2, 4, 4, 0.95);
  --card-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.3), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
  --card-shadow-hover: 0 10px 15px -3px rgba(0, 0, 0, 0.4), 0 4px 6px -2px rgba(0, 0, 0, 0.3);
  --text-dark: #ffffff;
  --text-medium: #e2e8f0;
  --text-light: #b0b0b0;
}

/* Utility Classes */
.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 30px;
}

.section {
  padding: 60px 0;
  background-color: var(--section-bg);
  transition: all 0.3s ease;
}

.section-header {
  text-align: center;
  margin-bottom: 50px;
}

.section-title {
  font-size: 3.2rem;
  font-weight: 700;
  color: var(--text-dark);
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: all 0.3s ease;
}

.section-subtitle {
  font-size: 1.4rem;
  color: var(--text-medium);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
  transition: all 0.3s ease;
}

/* Button Styles */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 16px 32px;
  font-size: 1.1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-decoration: none;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.btn-primary {
  background-color: var(--accent-blue);
  color: #ffffff;
  border-color: var(--accent-blue);
}

.btn-primary:hover {
  background-color: transparent;
  color: var(--accent-blue);
  border-color: var(--accent-blue);
}

.btn-secondary {
  background-color: transparent;
  color: var(--accent-gold);
  border-color: var(--accent-gold);
}

.btn-secondary:hover {
  background-color: var(--accent-gold);
  color: #ffffff;
}

/* Responsive Design */
@media (max-width: 1400px) {
  .container {
    max-width: 1400px;
    padding: 0 25px;
  }
}

@media (max-width: 1200px) {
  .container {
    max-width: 1200px;
    padding: 0 20px;
  }
}

@media (max-width: 768px) {
  .section {
    padding: 50px 0;
  }
  
  .section-header {
    margin-bottom: 40px;
  }
  
  .section-title {
    font-size: 2.5rem;
  }
  
  .section-subtitle {
    font-size: 1.2rem;
  }
  
  .btn {
    padding: 14px 28px;
    font-size: 1rem;
  }
  
  .container {
    max-width: 600px;
    padding: 0 15px;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 40px 0;
  }
  
  .section-header {
    margin-bottom: 30px;
  }
  
  .section-title {
    font-size: 2.2rem;
  }
  
  .section-subtitle {
    font-size: 1.1rem;
  }
  
  .btn {
    padding: 12px 24px;
    font-size: 0.95rem;
  }
  
  .container {
    padding: 0 12px;
  }
}
