.about {
  background-color: var(--primary-light);
  position: relative;
}

.about::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--accent-blue), var(--accent-gold), var(--accent-teal));
}

.about-content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 25px;
  align-items: stretch;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 30px;
}

.about-card,
.highlights-card,
.education-card {
  background-color: var(--primary-dark);
  border: 2px solid var(--accent-blue);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 450px;
  box-shadow: var(--card-shadow);
  transition: all 0.3s ease;
}

.about-card:hover,
.highlights-card:hover,
.education-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--card-shadow-hover);
}

.card-header {
  background-color: var(--accent-blue);
  padding: 20px 25px;
  display: flex;
  align-items: center;
  gap: 12px;
  border-bottom: 2px solid var(--accent-gold);
  flex-shrink: 0;
}

.card-icon {
  font-size: 1.8rem;
  color: #ffffff;
}

.card-header h3 {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

.card-body {
  padding: 25px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.card-body p {
  color: var(--text-medium);
  line-height: 1.6;
  margin-bottom: 20px;
  font-size: 1.15rem;
}

.card-body p:last-child {
  margin-bottom: 0;
}

.highlights-list {
  list-style: none;
  padding: 0;
  margin: 0;
  flex: 1;
}

.highlights-list li {
  color: var(--text-medium);
  padding: 15px 0;
  border-bottom: 1px solid var(--border-color);
  position: relative;
  padding-left: 25px;
  font-size: 1.1rem;
  line-height: 1.6;
}

.highlights-list li:last-child {
  border-bottom: none;
}

.highlights-list li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 20px;
  width: 10px;
  height: 10px;
  background-color: var(--accent-gold);
}

.education-list {
  padding: 25px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.education-item {
  padding: 25px 0;
  border-bottom: 1px solid var(--border-color);
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.education-item:last-child {
  border-bottom: none;
}

.education-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 12px;
}

.education-header h4 {
  color: var(--text-dark);
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0;
  line-height: 1.3;
}

.education-period {
  color: var(--accent-gold);
  font-size: 1.05rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  white-space: nowrap;
}

.education-details {
  color: var(--text-medium);
}

.school-name {
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--accent-teal);
  font-size: 1.15rem;
}

.location {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 1.05rem;
  color: var(--text-medium);
  font-weight: 500;
}

@media (max-width: 1400px) {
  .about-content {
    max-width: 1400px;
    padding: 0 25px;
    gap: 20px;
  }
}

@media (max-width: 1200px) {
  .about-content {
    max-width: 1200px;
    padding: 0 20px;
    gap: 20px;
  }
  
  .card-body,
  .education-list {
    padding: 20px;
  }
  
  .card-header {
    padding: 18px 20px;
  }
}

@media (max-width: 1024px) {
  .about-content {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    max-width: 1000px;
  }
  
  .highlights-card {
    grid-column: span 2;
  }
  
  .about-card,
  .highlights-card,
  .education-card {
    min-height: 400px;
  }
}

@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 20px;
    max-width: 600px;
    padding: 0 15px;
  }
  
  .highlights-card {
    grid-column: span 1;
  }

  .card-header {
    padding: 15px 20px;
  }

  .card-body,
  .education-list {
    padding: 20px;
  }

  .education-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .about-card,
  .highlights-card,
  .education-card {
    min-height: auto;
  }
}

@media (max-width: 480px) {
  .about-content {
    padding: 0 12px;
    gap: 15px;
  }
  
  .card-header {
    padding: 12px 15px;
  }

  .card-body,
  .education-list {
    padding: 15px;
  }

  .card-header h3 {
    font-size: 1.3rem;
  }

  .card-body p {
    font-size: 1rem;
  }
}
