.contact {
  background-color: var(--section-bg);
  position: relative;
  padding: 60px 0;
}

.contact::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--accent-blue), var(--accent-gold), var(--accent-teal));
}

.contact-content {
  margin-top: 40px;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 25px;
  max-width: 1200px;
  margin: 0 auto;
  justify-items: center;
}

.contact-item {
  background-color: var(--card-bg);
  border: 2px solid var(--accent-blue);
  padding: 30px 25px;
  display: flex;
  align-items: center;
  gap: 20px;
  transition: all 0.3s ease;
  box-shadow: var(--card-shadow);
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 380px;
  min-height: 120px;
}

.contact-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(180deg, var(--accent-blue), var(--accent-gold));
  transform: scaleY(0);
  transition: transform 0.3s ease;
}

.contact-item:hover::before {
  transform: scaleY(1);
}

.contact-item:hover {
  border-color: var(--accent-gold);
  transform: translateY(-5px);
  box-shadow: var(--card-shadow-hover);
}

.contact-icon {
  width: 55px;
  height: 55px;
  background-color: var(--accent-blue);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  transition: all 0.3s ease;
  border-radius: 4px;
}

.contact-item:hover .contact-icon {
  background-color: var(--accent-gold);
  transform: rotate(5deg) scale(1.1);
}

.contact-icon svg {
  font-size: 1.8rem;
  color: #ffffff;
  transition: all 0.3s ease;
}

/* Invert phone icon horizontally */
.contact-item:nth-child(2) .contact-icon svg {
  transform: scaleX(-1);
}

.contact-details {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  min-width: 0;
}

.contact-label {
  color: var(--text-dark);
  font-size: 1.3rem;
  font-weight: 700;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 1.2;
}

.contact-link,
.contact-value {
  color: var(--accent-blue);
  font-weight: 600;
  font-size: 1.05rem;
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-block;
  padding: 4px 0;
  border-bottom: 2px solid transparent;
  word-break: break-word;
  line-height: 1.4;
}

.contact-link:hover {
  color: var(--accent-gold);
  border-bottom-color: var(--accent-gold);
  transform: translateX(5px);
}

.contact-value {
  color: var(--text-dark);
  border-bottom-color: var(--border-color);
}

.contact-value:hover {
  border-bottom-color: var(--accent-gold);
}

/* Responsive Design */
@media (max-width: 1200px) {
  .contact-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    max-width: 1000px;
  }
  
  .contact-item {
    max-width: 350px;
    padding: 25px 20px;
  }
}

@media (max-width: 768px) {
  .contact {
    padding: 50px 0;
  }
  
  .contact-content {
    margin-top: 30px;
  }
  
  .contact-grid {
    grid-template-columns: 1fr;
    max-width: 500px;
    gap: 18px;
  }
  
  .contact-item {
    padding: 25px 20px;
    gap: 18px;
    max-width: none;
    min-height: 110px;
  }
  
  .contact-icon {
    width: 50px;
    height: 50px;
  }
  
  .contact-icon svg {
    font-size: 1.6rem;
  }
  
  .contact-label {
    font-size: 1.2rem;
  }
  
  .contact-link,
  .contact-value {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .contact {
    padding: 40px 0;
  }
  
  .contact-grid {
    gap: 15px;
  }
  
  .contact-item {
    padding: 20px 15px;
    gap: 15px;
    min-height: 100px;
  }
  
  .contact-icon {
    width: 45px;
    height: 45px;
  }
  
  .contact-icon svg {
    font-size: 1.4rem;
  }
  
  .contact-label {
    font-size: 1.1rem;
  }
  
  .contact-link,
  .contact-value {
    font-size: 0.95rem;
  }
}
