.App {
  min-height: 100vh;
  background-color: var(--primary-dark);
  color: var(--text-dark);
  transition: all 0.3s ease;
}

.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--primary-dark);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: background-color 0.3s ease;
}

.loading-animation {
  display: flex;
  justify-content: center;
  align-items: center;
}

.initials-container {
  position: relative;
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: pulse 1.5s ease-in-out infinite;
  transition: all 0.3s ease;
}

.initials-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.initials-fallback {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ffffff;
  font-size: 2rem;
  font-weight: 700;
  font-family: 'Inter', sans-serif;
  letter-spacing: 2px;
  display: none; /* Hidden by default, shown if image fails */
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
}

main {
  width: 100%;
}

/* Smooth scrolling for anchor links */
html {
  scroll-behavior: smooth;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary-light);
}

::-webkit-scrollbar-thumb {
  background: var(--accent-blue);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-teal);
}
