.experience {
  background-color: var(--section-bg);
  position: relative;
}

.experience::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--accent-teal), var(--accent-blue), var(--accent-gold));
}

.experience-timeline {
  display: flex;
  flex-direction: column;
  gap: 25px;
  position: relative;
}

.experience-timeline::before {
  content: '';
  position: absolute;
  left: 25px;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(180deg, var(--accent-blue), var(--accent-teal), var(--accent-gold));
}

.experience-item {
  position: relative;
  padding-left: 60px;
}

.experience-item::before {
  content: '';
  position: absolute;
  left: 16px;
  top: 25px;
  width: 18px;
  height: 18px;
  background-color: var(--accent-blue);
  border: 3px solid var(--card-bg);
  z-index: 2;
}

.experience-item:nth-child(even)::before {
  background-color: var(--accent-teal);
}

.experience-item:nth-child(3n)::before {
  background-color: var(--accent-gold);
}

.experience-card {
  background-color: var(--card-bg);
  border: 2px solid var(--accent-blue);
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: var(--card-shadow);
}

.experience-card:hover {
  border-color: var(--accent-gold);
  transform: translateX(10px);
  box-shadow: var(--card-shadow-hover);
}

.experience-header {
  background-color: var(--accent-blue);
  padding: 20px 25px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 15px;
  border-bottom: 2px solid var(--accent-gold);
}

.experience-title-section {
  flex: 1;
  min-width: 280px;
}

.experience-title {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.experience-company {
  display: flex;
  align-items: center;
  gap: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
  font-size: 1.15rem;
}

.experience-company svg {
  color: var(--accent-gold);
  font-size: 1.05rem;
}

.experience-meta {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-end;
  min-width: 180px;
}

.experience-period,
.experience-location {
  display: flex;
  align-items: center;
  gap: 6px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  font-weight: 500;
}

.experience-period svg,
.experience-location svg {
  color: var(--accent-gold);
  font-size: 0.9rem;
}

.experience-type {
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 4px 10px;
  border: 2px solid #ffffff;
}

.experience-content {
  padding: 25px;
}

.achievements-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.achievement-item {
  color: var(--text-medium);
  padding: 12px 0;
  border-bottom: 1px solid var(--border-color);
  position: relative;
  padding-left: 20px;
  font-size: 1.1rem;
  line-height: 1.6;
}

.achievement-item:last-child {
  border-bottom: none;
}

.achievement-item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 18px;
  width: 6px;
  height: 6px;
  background-color: var(--accent-gold);
}

@media (max-width: 768px) {
  .experience-timeline::before {
    left: 18px;
  }

  .experience-item {
    padding-left: 50px;
  }

  .experience-item::before {
    left: 10px;
    width: 16px;
    height: 16px;
  }

  .experience-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .experience-title-section {
    min-width: auto;
  }

  .experience-meta {
    align-items: flex-start;
    min-width: auto;
  }

  .experience-title {
    font-size: 1.4rem;
  }

  .experience-company {
    font-size: 1.1rem;
  }

  .experience-content {
    padding: 20px;
  }

  .achievement-item {
    font-size: 1.05rem;
    padding-left: 18px;
  }
}

@media (max-width: 480px) {
  .experience-timeline::before {
    left: 12px;
  }

  .experience-item {
    padding-left: 40px;
  }

  .experience-item::before {
    left: 4px;
    width: 14px;
    height: 14px;
  }

  .experience-header {
    padding: 15px 20px;
  }

  .experience-content {
    padding: 15px 20px;
  }

  .experience-title {
    font-size: 1.3rem;
  }

  .achievement-item {
    font-size: 1rem;
    padding-left: 15px;
  }
}
